import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { RecipiePage } from './recipie.page';

const routes: Routes = [
  {
    path: '',
    component: RecipiePage
  },
  {
    path: 'create-recipe',
    loadChildren: () => import('./create-recipe/create-recipe.module').then( m => m.CreateRecipePageModule)
  },
  {
    path: ':recipeId',
    loadChildren: () => import(`./recipie-detail/recipie-detail.module`).then(m => m.RecipieDetailPageModule)
  },
 
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RecipiePageRoutingModule {}
